.footer{
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #F4F2F0;
  &__top{
    margin-bottom: 20px;
  }
}

.socials {
  display: flex;
  &__link{
    margin-right: 20px;
    opacity: .4;
    transition: opacity .4s;
    &:hover{
      opacity: 1;
    }
    &:last-child{
      margin-right: 0px;
    }
  }
}