.fast-form{
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #F6F4F3;
  padding-top: 40px;
  padding-bottom: 40px;
}

.fast-form__info{
  max-width: 250px;
}

.fast-form__undertitle{
  font-size: 12px;
}

.fast-form__submit{
  margin-right: 12px;
}

.fast-form__description{
  max-width: 300px;
  font-size: 12px;
  color: #838686;
  a{
    color: #838686;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}