.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
  width: 100%;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}

.page{
  padding-top: 160px;
  padding-bottom: 60px;
  &.page-no-padding-bottom{
    padding-bottom: 0px;
  }
}

.text-small{
  font-size: 14px;
}

.text-grey{
  color: #B5B7B6;
}

.text-bold{
  font-weight: bold;
}







h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
}


h1.h1{
  font-size: 38px;
  line-height: 51px;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 40px;
}

h2.h2{
  font-size: 26px;
  line-height: 120%;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
  margin-bottom: 20px;
}


h5.h5{
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.04em;
  font-family: $playfair;
  text-transform: uppercase;
}