.header{
  margin-bottom: 72px;
  &__logo{
    margin-right: 40px;
  }
}

.header--transparent {
  background: rgba(254, 250, 247, 0.7);
  backdrop-filter: blur(56px);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  height: 87px;
  .container{
    height: 87px;
  }
  .header__left{
    padding-left: 130px;
  }
  .header__logo{
    position: absolute;
    left: 85px;
    top: 0;
  }
}

.header__contacts{
  margin-right: 120px;
}

.header__contact{
  font-size: 16px;
  margin-bottom: 2px;
  line-height: 19px;
  b{
    font-weight: bold;
  }
}


.header__contact--small{
  font-size: 14px;
}

.burger{
  width: 32px;
  margin-left: 26px;
  .svgsprite._burger{
    width: 32px;
  }
}