.main-slider{
  margin-bottom: 80px;
  img{
    width: 100%;
  }
  .swiper-pagination-bullets .swiper-pagination-bullet{
    margin: 0px;
    margin-right: 20px;
  }
  .swiper-pagination{
    display: inline-flex;
    align-items: center;
    width: auto;
    left: 85px;
    bottom: 60px;
    
  }

  .swiper-pagination-bullet{
    width: 40px;
    height: 6px;
    border-radius: 20px;
    background-color: #fff;
    opacity: .5;
    transition: .4s;
    margin-right: 20px;
    &.swiper-pagination-bullet-active{
      width: 80px;
      height: 10px;
      background-color: #FBF9F7;
      opacity: 1;
    }
    
  }
  
}

.phone{
  z-index: 200;
  position: fixed;
  width: 72px;
  height: 72px;
  right: 85px;
  bottom: 80px;
  border-radius: 50%;
  background-color: #F1F1F1;
  box-shadow: 0px 0px 20px -8px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: center;
  align-items: center;
}

.blogs__list{
  display: flex;
  flex-wrap: wrap;
  margin-right: -30px;
  padding-bottom: 60px;
}


.blog-panel{
  height: 340px;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 20px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  &:hover:before{
    opacity: 1;
  }
  &:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(250, 248, 245, 0.6);
    backdrop-filter: blur(56px);
    opacity: 0;
    transition: .6s;
  }
  &--33{
    width: calc(33.33% - 30px);
  }
  &--50{
    width: calc(50% - 30px);
  }
  &--66{
    width: calc(66.67% - 30px);
  }
  &--100{
    width: calc(100% - 30px);
  }
}


.blog-panel__tag{
  position: absolute;
  left: 20px;
  top: 20px;
  height: 22px;
  padding-left: 16px;
  padding-right: 16px;
  border-radius: 20px;
  background-color: #FBF9F7;
  margin-right: 10px;
  transition: .6s;
  .blog-panel:hover &{
    background-color: #080E0D;
    color: #FBF9F7;
  }
}


.blog-panel__info{
  padding-left: 20px;
  padding-right: 20px;
  position: absolute;
  left: 0;
  bottom: 40px;
  transition: .4s;
  width: 100%;
  color: #FBF9F7;
  transform: translateY(-23px);
  .blog-panel:hover &{
    transform: translateY(0px);
  }
}

.blog-panel__date{
  margin-bottom: 10px;
  transition: .4s;
  .blog-panel:hover &{
    opacity: 0;
  }
}

.blog-panel__title{
  font-family: $playfair;
  margin-bottom: 8px;
  .blog-panel:hover &{
    color: #080E0D;
  }
}

.blog-panel__text{
  margin-bottom: 20px;
  color: #838686;
  margin-bottom: 20px;
  max-width: 736px;
  transition: .4s;
}

.blog-panel__link{
  transition: .4s;
  color: #363838;
  font-weight: bold;
}

.blog-panel__bottom{
  margin-bottom: -100%;
  transition: .6s;
  .blog-panel:hover &{
    margin-bottom: 0;
  }
}

.develop{
  display: flex;
  align-items: center;
  svg{
    margin-left: 8px;
    transform: translateY(2px);
  }
}