.svgsprite {
    display:inline-block;
    fill:currentColor;
    vertical-align:middle;

    &._burger {
    	width:32px;
    	height:14px;
    }
    &._crumbs-back {
    	width:39px;
    	height:9px;
    }
    &._develop {
    	width:98px;
    	height:17px;
    }
    &._next {
    	width:24px;
    	height:24px;
    }
    &._phone {
    	width:24px;
    	height:23px;
    }
    &._prev {
    	width:24px;
    	height:24px;
    }
    &._socials-1 {
    	width:10px;
    	height:19px;
    }
    &._socials-2 {
    	width:19px;
    	height:19px;
    }
    &._socials-3 {
    	width:26px;
    	height:17px;
    }
}