.input{
  font-size: 14px;
  input{
    width: 100%;
    height: 45px;
    border-radius: 10px;
    border: 1px solid #363838;
    z-index: 10;
    padding-left: 20px;
  }
}

.input__placeholder{
  position: absolute;
  left: 16px;
  top: 14px;
  color: #B5B7B6;
  padding-left: 4px;
  padding-right: 4px;
  transition: .4s;
  input:focus + &{
    transform: translateY(-23px)scale(.9);
    z-index: 20;
    background-color: #F6F4F3;
  }
}

.fast-form__item{
  margin-bottom: 28px;
  &:last-child{
    margin-bottom: 0px;
  }
}

.select__main{
  background-color: #F4F2F0;
  border-radius: 20px;
  height: 41px;
  display: inline-flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 14px;
  cursor: pointer;
  &:after{
    content: "";
    position: relative;
    width: 15px;
    height: 16px;
    background: url(../img/forms/select.svg) no-repeat center / contain;
    margin-left: 6px;
  }
}